<template>
    <div class="leave-list">
        <div class="table-head">
            <el-button type="default" class="left" size="small" @click="delMsgFunc()" :disabled="tableData && tableData.hasOwnProperty('list') && tableData.list.length <= 0">{{ $L('选中删除') }}
            </el-button>
            <div class="right">
                <el-select class="search" v-model="messageType" size="small"
                           @change="changeTypeFunc">
                    <el-option
                            v-for="item in messageTypelist"
                            :key="item.type"
                            :label="item.name"
                            :value="item.type">
                    </el-option>
                </el-select>
                <el-input :placeholder="$L('输入留言')" size="small" v-model="inputValue"
                          class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <div class="content" v-if="tableData && tableData.hasOwnProperty('list')">
            <el-table
                    :data="tableData.list"
                    style="width: 100%" class="table-box"
                    :header-cell-style="{background:'#f0f1f5',color:'#666'}"
                    :empty-text="$L('暂无数据')"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        :label="$L('留言')"
                        min-width="250px"
                        align="left">
                    <div slot-scope="scope" class="status">
                        <span v-html="scope.row.content"></span><br>

                        <div v-if="scope.row.chrReply !== '' && scope.row.isRred" class="get-message"
                             @click="hideMsgFunc(scope.row)">
                            <p class="font-grey">{{$L('商家回复')}}：<span v-html="scope.row.chrReply"></span></p>
                            <p class="font-grey">{{ scope.row.dtReply }}</p>
                        </div>
                        <div class="right-btns">
                            <el-button v-if="scope.row.status === 1 && setMsgInfo" type="text"
                                       @click="getMsgFunc(scope.row)">[{{ $L('查看回复') }}]
                            </el-button>
                            <el-button v-if="scope.row.status === 1 && !setMsgInfo" type="text"
                                       @click="hideMsgFunc(scope.row)">[{{ $L('点击收起') }}]
                            </el-button>
                        </div>

                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('时间')"
                        align="center">
                    <div slot-scope="scope">
                        {{ scope.row.dtCreate }}
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('状态')"
                        align="center">
                    <div slot-scope="scope" class="status">
                        <p v-bind:class="statusForm(scope.row.status).color">{{
                            statusForm(scope.row.status).text}}</p>
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('操作')"
                        align="center">
                    <div slot-scope="scope" class="operate">
                        <el-button type="primary" @click="delMsgFunc(scope.row.id)">{{ $L('删除') }}</el-button>
                    </div>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        layout="prev, pager, next, total, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "leaveList",
        data () {
            return {
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                messageType: 0,
                messageTypelist: [
                    {
                        type: 0,
                        name: this.$L('全部消息')
                    },
                    {
                        type: 2,
                        name: this.$L('未回复')
                    },
                    {
                        type: 1,
                        name: this.$L('已回复')
                    },
                ],
                routerData: {},
                tableData: [],
                pageSize: 20,
                currentPage: 1,
                type: 0,
                inputValue: '',
                msgArr: [],
                dialogVisible: false,
                textarea: '',
                setMsgInfo: true,
            }
        },
        created() {
            this.getData();
        },
        methods: {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            changeTypeFunc(val) {
                this.type = val;
                this.getData();
            },
            statusForm(value) {
                let x;
                switch (value) {
                    case 0:
                        x = {text: this.$L('未回复'), color: 'font-red'};
                        break;
                    default:
                        x = {text: this.$L('已回复'), color: 'font-grey'};
                        break;
                }
                return x;
            },
            getMsgFunc(row) {
                row.isRred = true;
                this.setMsgInfo = false;
            },
            hideMsgFunc(row) {
                row.isRred = false;
                this.setMsgInfo = true;
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    type: _self.type,
                    keyword: _self.inputValue
                };
                _self.$api('getLeaveList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            handleSelectionChange(val) {
                let arr = [];
                for (let i in val) {
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            delMsgFunc(id) {
                const _self = this;
                if (!id && (Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中留言信息，请勾选留言信息再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('取消'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(() => {

                    });
                    return;
                }

                let leaveIDs;

                if (id) {
                    leaveIDs = id;
                } else {
                    leaveIDs = this.msgArr;
                }

                let arr = {
                    leaveIDs: leaveIDs
                };
                _self.$confirm(_self.$L('此操作将永久删除该留言信息, 是否继续?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteLeaveMsg', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }).catch(() => {

                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
    .leave-list{
        .table-head {
            height: 32px;
            margin-bottom: 10px;
            .left {
                float: left;
            }

            .right {
                float: right;

                .font {
                    padding-left: 20px;
                    font-size: 16px;
                }

                .search {
                    width: 120px;
                }

                .input-with-select {
                    width: calc(100% - 120px);
                    padding-left: 10px;
                }
            }
        }

        .content {
            background: #fff;
            padding: 20px;
            .table-box {
                padding: 0;

                .status {
                    .font-red {
                        color: #f00;
                    }

                    .font-grey {
                        color: #999;
                    }

                    .get-message {
                        padding-top: 10px;
                    }

                    .right-btns {
                        text-align: right;
                    }
                }

                .operate {
                    button {
                        padding: 8px 28px;
                    }
                }
            }
            .list-pages {
                text-align: center;
                padding: 40px 0 20px;
            }
            /deep/ .el-table__empty-block {
                @extend .no-select;
                position: relative;
                width: 100%;
                height: 380px;
                line-height: 380px;
                text-align: center;
                background-color: #ffffff;
                font-size: 16px;
                color: #999;
                margin-bottom: 20px;
            }
        }

        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }

</style>